import Layout from "../components/MyLayout.js";
import axios from "axios";

class Index extends React.Component {
  state = {
    blog: []
  };

  componentDidMount() {
    axios.get(`static/content/blog.json`).then(res => {
      const blog = res.data;
      this.setState({ blog });
    });
  }

  render() {
    const blogItems = Object.keys(this.state.blog).map((item, index) => {
      const blogItem = this.state.blog[item];
      return blogItem;
    });

    return (
      <Layout>
        <p>Home</p>
        {Object.keys(blogItems).map((item, index) => {
          console.log(blogItems[item][index]);
        })}
      </Layout>
    );
  }
}

export default Index;
